.player-controller {
  padding: 20px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.player-controller button {
  color: #555;
}

.player-controller-time,
.player-controller-duration {
  padding: 5px;
  color: #999;
}

.player-seekbar {
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}

.prev-btn,
.next-btn {
  font-size: 1.5em;
  background: none;
  border: none;
  margin-left: .5em;
  margin-right: .5em;
}

.play-btn,
.pause-btn {
  font-size: 2em;
  background: none;
  border: none;
  padding: .5em;
}

.player-volume-icon {
  padding-left: 1em;
  padding-right: .2em;
}


